<script>
export default {
  name: "LoginView",
  data(){
    return{
      id: "",
      password: ""
    }


  },
  created() {
  },
  inject:['$http'],
  methods:{
    login(){
      let loginInfo = {};
      loginInfo.id = this.id;
      loginInfo.password = this.password;
      this.$http.post('/account/login', loginInfo).then(res =>{
        console.log(res.data);
      }).catch(err => {
        console.log(err);
      });
    },


  }
}

</script>

